body {
  background-color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.separation {
  margin-top: 35rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .container__grid {
    display: grid;
    grid-template-columns: 5fr 2fr;
    column-gap: 2rem;
  }
}

.heart {
  color: #cc1935;
}

.visible {
  display: none;
}

.error {
  background-color: #ae152d;
  border: 1px solid #530a15;
}

.success {
  background-color: #1ca836;
  border: 1px solid #0d511a;
}

.photo img {
  border: 0.4rem solid #ffffff;
  border-radius: 0.6rem;
  -webkit-box-shadow: 0.3rem 0 0.3rem 0.3rem #ccc;
  box-shadow: 0.3rem 0 0.3rem 0.3rem #ccc;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1,
h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #111;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.btn {
  border: none;
  border-radius: 0.5rem;
  box-shadow: none;
  cursor: pointer;
  font-weight: 700;
  padding: 1rem 1.5rem;
  text-align: center;
}

.btn-block {
  width: 100%;
}
@media (min-width: 768px) {
  .btn-block {
    width: auto;
  }
}

.btn-info,
.btn-edit,
.btn-delete,
.btn-icon,
.btn-default,
.btn-success {
  color: #ffffff;
}

.btn-edit,
.btn-default,
.btn-success,
.btn-delete {
  display: inline-block;
}

.btn-info {
  background-color: #0784c9;
}

.btn-edit {
  background-color: #ff9436;
}

.btn-default {
  background-color: #aaa;
}

.btn-success {
  background-color: #1ca836;
}

.btn-delete {
  background-color: #ae152d;
}

.btn-icon {
  background-color: transparent;
}

.btn-info:hover,
.btn-edit:hover,
.btn-delete:hover,
.btn-default:hover,
.btn-success:hover {
  background-color: #ffffff;
}

.btn-info:hover {
  border: 1px solid #0784c9;
  color: #0784c9;
}

.btn-edit:hover {
  border: 1px solid #ff9436;
  color: #ff9436;
}

.btn-delete:hover {
  border: 1px solid #ae152d;
  color: #ae152d;
}

.btn-default:hover {
  border: 1px solid #aaa;
  color: #aaa;
}

.btn-success:hover {
  border: 1px solid #1ca836;
  color: #1ca836;
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/bg_header.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  border-bottom: 0.4rem solid #0784c9;
  height: 30rem;
  padding: 5rem 0;
  width: 100%;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}
.header__link {
  color: #ffffff;
  text-align: center;
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
  }
}
.navigation__link {
  color: #ffffff;
  font-size: 2.4rem;
  transition: color 0.3s;
}
.navigation__link:hover {
  color: #0784c9;
}

.homes__heading {
  background-image: url(../img/bg-title.png);
  background-repeat: repeat-x;
  background-position-y: center;
  text-align: center;
}
.homes__heading--text {
  background-color: #ffffff;
  padding: 0 2rem;
}
.homes__subheading {
  text-align: center;
}
.homes__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .homes__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.home__photo {
  transition: transform 0.3s ease-in-out;
}
.home__photo:hover {
  transform: scale(110%);
}
.home__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}
.home__heading {
  font-weight: 700;
}
.home__text {
  font-size: 1.8rem;
  font-weight: 700;
}
.home__clock {
  font-size: 1.8rem;
  font-weight: 700;
}
.home__clock--normal {
  font-size: 1.2rem;
  font-weight: 400;
}

.albums {
  position: relative;
}
.albums__heading {
  background-image: url(../img/bg-title.png);
  background-repeat: repeat-x;
  background-position-y: center;
  text-align: center;
}
.albums__heading--text {
  background-color: #ffffff;
  padding: 0 2rem;
}
.albums__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .albums__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .albums__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1200px) {
  .albums__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.album__photo {
  margin: 0;
  transition: transform 0.3s ease-in-out;
}
.album__photo:hover {
  transform: scale(1.1);
}
.album__image {
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.album__image:hover {
  cursor: pointer;
  filter: grayscale(20%);
  transition: filter 0.3s;
}
.album__content {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
}
.album__icon {
  font-size: 2rem;
  margin: 0;
}

.photo__heading {
  background-image: url(../img/bg-title.png);
  background-repeat: repeat-x;
  background-position-y: center;
  text-align: center;
}
.photo__heading--text {
  background-color: #ffffff;
  padding: 0 2rem;
}
.photo__content {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1.5rem;
}
.photo__comments {
  padding: 2rem;
}
.photo__block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.photo__grid {
  padding: 3rem;
}
.photo__comment {
  border-bottom: 1px solid #ccc;
  margin-bottom: 1rem;
}

.comments__heading {
  text-align: center;
}
.comments__buttons {
  margin: 1rem 0;
}
.comments__subheading {
  text-align: center;
}
.comments__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .comments__grid {
    display: grid;
    grid-template-columns: 3fr 1fr;
    column-gap: 2rem;
  }
}

@media (min-width: 768px) {
  .commentlist__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.commentlist__buttons {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .commentlist__buttons {
    flex-direction: row;
    justify-content: space-between;
  }
}
.commentlist__form {
  display: inline-block;
}
.commentlist__link {
  display: inline-block;
}

.about__heading {
  background-image: url(../img/bg-title.png);
  background-repeat: repeat-x;
  background-position-y: center;
  text-align: center;
}
.about__heading--text {
  background-color: #ffffff;
  padding: 0 2rem;
}
.about__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .about__grid {
    grid-template-columns: 3fr 2fr;
  }
}
.about__content {
  background-color: #ffffff;
  padding: 0 2rem;
}
.about__link {
  font-weight: 700;
}

.sidebar__content {
  margin-bottom: 3rem;
}
.sidebar__heading {
  margin-bottom: 1rem;
}
.sidebar__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.sidebar__image {
  border: 4px solid #ffffff;
  transition: transform 0.3s ease-in-out;
}
.sidebar__image:hover {
  transform: scale(110%);
}

.comment {
  display: grid;
  grid-template-columns: 1fr 3fr;
  column-gap: 2rem;
}
.comment__photo {
  border: 2px solid #ffffff;
  transition: transform 0.3s ease-in-out;
}
.comment__photo:hover {
  transform: scale(110%);
}
.comment__text {
  margin: 0;
}
.comment__author {
  font-size: 1.3rem;
  font-weight: 700;
  text-align: right;
}

.form {
  background-color: #ffffff;
  margin: 0 auto;
  padding: 3rem 0;
  width: 60rem;
}
.form__heading {
  text-align: center;
}
.form__messages {
  margin: 0 auto 2rem auto;
  padding: 0 2rem;
  position: relative;
  width: 85%;
}
.form__icon {
  position: absolute;
  right: 0;
}
.form__message {
  color: #ffffff;
  font-weight: 700;
  text-align: center;
}
.form__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form__formulary {
  border: 1px solid #ccc;
  border-radius: 1rem;
  padding: 2rem;
  width: 80%;
}
.form__group {
  margin-bottom: 1.5rem;
}
.form__radios {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.form__text {
  color: #111;
  display: inline-block;
  font-weight: 700;
  margin-bottom: 0.5rem;
  text-align: center;
}
.form__label {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.form__input {
  border: 1px solid #0784c9;
  display: block;
  padding: 2rem;
  width: 100%;
}
.form__input:focus {
  border: 0.2rem solid #0784c9;
  outline: none;
}
.form__link {
  display: inline-block;
  margin: 2rem 0 0 2rem;
  transition: all 0.3s ease-in-out;
}
.form__link:hover {
  color: #0784c9;
  font-weight: 700;
}

.items__heading {
  text-align: center;
}
.items__buttons {
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .items__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.items__link {
  display: block;
  margin-bottom: 2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .items__link {
    display: inline-block;
    width: auto;
  }
}
.items__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}

@media (min-width: 768px) {
  .item {
    display: grid;
    grid-template-columns: 2fr 3fr 3fr;
    column-gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .item__image {
    width: 30%;
  }
}
.item__heading {
  font-weight: 700;
  text-align: center;
}
@media (min-width: 768px) {
  .item__operations {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.users__layout {
  width: 100%;
}
@media (min-width: 768px) {
  .users__layout {
    max-width: 50rem;
    margin: 0 auto;
  }
}
.users__heading {
  text-align: center;
}
.users__button {
  margin: 1rem 0;
}

@media (min-width: 768px) {
  .user {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: 3fr 1fr;
    align-items: center;
  }
}
.user__content {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .user__content {
    display: grid;
    gap: 2erm;
    grid-template-columns: repeat(2, 1fr);
  }
}
.user__title {
  font-weight: 700;
  text-align: center;
}
.user__button {
  text-align: center;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(57, 70, 89, 0.7);
  display: flex;
  opacity: 0;
  pointer-events: none;
  transition: opaciy 0.6s 0.9s;
  --transform: translateY(-100vh);
  --transition: transform 0.8s;
}
.modal--show {
  opacity: 1;
  pointer-events: unset;
  transition: opaciy 0.6s;
  --transform: translateY(0);
  --transition: transform 0.8s 0.8s;
}
.modal__container {
  background-color: #ffffff;
  border-radius: 0.6rem;
  display: grid;
  gap: 1rem;
  grid-auto-columns: 100%;
  margin: auto;
  max-width: 600px;
  padding: 2rem 1.5rem;
  place-items: center;
  transform: var(--transform);
  transition: var(--transition);
  width: 90%;
}
@media (min-width: 768px) {
  .modal__container {
    padding: 3rem 2.5rem;
  }
}
.modal__image {
  max-width: 30rem;
  width: 90%;
}
.modal__links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 25rem;
}
@media (min-width: 768px) {
  .modal__links {
    width: 50rem;
  }
}
.modal__heading {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .modal__heading {
    font-size: 2.5rem;
  }
}

.footer {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/bg_footer.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  border-top: 0.4rem solid #0784c9;
  height: 30rem;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.footer__link {
  color: #ffffff;
}
.footer__copyright {
  color: #ffffff;
  font-size: 2.4rem;
  margin: 6rem 0 0 0;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__copyright {
    margin-top: 0;
  }
}